import { f as forgotPassword } from "../../../../chunks/auth.js";
import { f as fail } from "../../../../chunks/_layout.server.js";
import { match, P } from "ts-pattern";
const actions = {
  forgotPassword: async ({ request }) => {
    const data = await request.formData();
    const rawEmail = data.get("email")?.toString();
    return match(rawEmail).with(P.string.includes("@"), forgotPassword).with(P.string, () => fail(400, {
      message: "Invalid email format",
      type: "failure"
    })).otherwise(() => ({
      type: "success"
    }));
  }
};
export {
  actions
};
